// 预约状态管理
export default {
  state: {
    reservations: []
  },
  
  getters: {
    reservations() {
      return this.state.reservations
    }
  },
  
  // 添加 getter 方法供外部调用
  getReservations() {
    return this.state.reservations
  },
  
  mutations: {
    setReservations(reservations) {
      this.state.reservations = reservations || []
    }
  },
  
  actions: {
    async fetchReservations() {
      const { getReservations } = require('../utils/api')
      const data = await getReservations()
      this.mutations.setReservations(data.reservations || data.list || data)
      return this.state.reservations
    }
  }
}

